

.Books__Section__Container{
  width:100%;
  height: 100%;
  border-top: 2px solid var(--BG);
  background: var(--BG) center/cover url("Imgs/layered-peaks-haikei\(5\).svg");
  display:flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 1rem;

  }

.Book__Container{
  position: relative;
  width:200px;
  height:300px;
  margin:2rem;
  margin-bottom: 0rem;
  background-color: var(--MC);
  overflow: hidden;
  -webkit-box-shadow: 0px 0px 3px 1px #000; 
  box-shadow: 0px 0px 3px 1px #000;
 
}

.Book__Container img{
  width:200px;
  height:200px;
  
}

.Book__Container h2, span{
  color:white;
}

.Book__Container h2{
  font-size: 1.2rem;
}
  
.Book__Desc{
  color:white;
  position: absolute;
  color:black;
  left: 0;
  right:0;
  bottom:0;
  font-size: 1rem;
  background-color: white;
  padding: 2rem;
  max-height: 100%;
  transform: translateY(101%);
  transition: transform 0.5s ease-in-out;
  overflow: auto;
}

.Book__Container:hover .Book__Desc{
  transform: translateY(0);
}