footer{
    padding-top: 2rem;
    width:100%;
    background: linear-gradient(to bottom ,var(--BG) 40%, var(--MC));
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
    color:white;
}

footer h3, .Social__Links{
    padding-bottom:1rem;
}

.Social__Links{
    flex-direction: row;
}

a{
    text-decoration: none;
    color:white;
}
a:hover{
    color:var(--HL)
}