.Hero__BG{
    aspect-ratio: 16/9;
    background:  center/cover url("./Imgs/pexels-yaroslav-shuraev-7041729.jpg") no-repeat;
    /* height:50%; */
}

.Top__Bar{
    display:flex;
    justify-content: space-between;
}

nav{
    display:flex;
    gap:1rem;
    justify-content: flex-end;
    padding-right: 1rem;
}

nav a{
    text-decoration: none;
    color:white;
}

nav a:hover{
    color:var(--MC);
}

.Title__Card{
   width:100%;
   height:100%;
   display:grid;
   grid-template-columns: 20px 1fr 1fr 1fr 20px;
   align-items: center;
   gap:3rem;
}

h1{
    color:white;
    font-size:3rem;

}




.Search__Container{
    grid-column:3;
    display: flex;
    /* justify-content: flex-end; */
    align-items: center;
    gap: 1rem;
    padding-right: 0.2rem;
    padding-top:2rem;
    height:300px;

}

.Search__Container{
    flex-direction: column;
}

.Search__Container input{
    width:20rem;
    
}

