} */

.Search__Container button{
    background-color: var(--MC);
    border:1px solid #fff;
    color:white;
    padding:0.5rem;
    width:4rem;
}

.Search__Container button:hover{
    -webkit-box-shadow: 0px 0px 15px 9px var(--MC); 
    box-shadow: 0px 0px 15px 9px var(--MC);
    border:2px solid var(--MC);

}